* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
ol,
li {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

.list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 60px 20px;
    max-width: 1400px;
    margin: 0 auto;
}

.list img {
    vertical-align: middle;
    max-width: 100%;
    width: 100%;
    height: 300px;
    object-fit: cover;
}

header {}

header h1 {
    line-height: 80px;
    border-bottom: 1px solid #ddd;
    text-align: center;
    background: #f9f9f9;
}

button {
    width: 80px;
    height: 30px;
    color: black;
    background: #ddd;
    border-radius: 50px;
    margin: 0 0 0 8px;
}

form {
    padding: 50px 30px;
    text-align: center;
}

input {
    height: 30px;
}